import "./App.css";
import React from "react";
import { Route, Switch, Link } from "react-router-dom";
import Calculator from "./Calculator";
import DrumMachine from "./DrumMachine";
import MarkdownPreviewer from "./MarkdownPreviewer";
import QuoteGenerator from "./QuoteGenerator";
import Home from "./Home";

class App extends React.Component {
  render() {
    return (
      <div className="App">
        <div>
          <nav>
            <ul id="navigation">
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <Link to="/calculator">Calculator</Link>
              </li>
              <li>
                <Link to="/drummachine">Drum Machine</Link>
              </li>
              <li>
                <Link to="/markdownpreviewer">Markdown Previewer</Link>
              </li>
              <li>
                <Link to="/quotegenerator">Quote Generator</Link>
              </li>
            </ul>
          </nav>
        </div>
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route exact path="/calculator">
            <Calculator />
          </Route>
          <Route exact path="/drummachine">
            <DrumMachine />
          </Route>
          <Route exact path="/markdownpreviewer">
            <MarkdownPreviewer />
          </Route>
          <Route exact path="/quotegenerator">
            <QuoteGenerator />
          </Route>
        </Switch>
      </div>
    );
  }
}

export default App;
